<template>
  <div class="scroll" ref="scrollRef">
    <div>
      <div class="load_icon">
        <div class="load_box" v-show="showLoad">
          <van-loading color="#e54427" />
        </div>
      </div>
      <slot></slot>
      <div class="load_bom">
        <van-loading v-show="showUpload" color="#e54427" size="24px">...</van-loading>
      </div>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll'
  export default {
    data() {
      return {
        scroll: null,
        showLoad: false,
        showUpload: false
      }
    },
    mounted() {
      // const self = this
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.scrollRef, {
          click: true,
          // 
          pullUpLoad: {
            // 30px pullingUp 
            threshold: -10,
            stop: -50
          },
          // 
          pullDownRefresh: {
            // 30pxpullingDown
            threshold: 30,
            // 20px
            stop: 50
          }
        })
        this.scroll.on('pullingUp', () => {
          console.log('')
          this.showUpload = true
          setTimeout(() => {
            //  better-scroll 
            this.scroll.finishPullUp()
            this.$emit('finishPullUp')
            this.showUpload = false
          }, 1000)
        })
        this.scroll.on('pullingDown', () => {
          console.log('')
          this.showLoad = true
          setTimeout(() => {
            console.log('asfsaf')
            //  better-scroll 
            this.showLoad = false
            this.scroll.finishPullDown()
            this.$emit('finishPullDown')
          }, 2000)
        })
      })
    }
  }

</script>

<style lang="less" scoped>
  .scroll {
    width: 100%;
    height: 75vh;
    position: relative;
    // padding-bottom: 80px;
  }

  .load_icon {
    position: absolute;
    left: 50%;
    top: -60px;
    transform: translateX(-50%);
    // background-color: #fff;
  }

  .load_box {
    background-color: #fff;
  }

  .load_bom {
    display: flex;
    justify-content: center;
    position: relative;
    bottom: 8px;
    // left: 50%;
    // transform: translateX(-50%);
    // margin: 0 auto;
    // top: -10px;
    // transform: translateX(-50%);
  }

</style>
